@use "../abstracts/variables";
@use "../abstracts/mixins";

.createurs {
  margin-bottom: variables.$margin-xxl;

  @include mixins.media('desktop') {
    margin-bottom: variables.$margin-section;
  }

  @include mixins.media('desktop') {
    min-width: 1160px;
  }

  &__title {
    text-align: center;
    font-weight: variables.$font-regular;

    @include mixins.media('tablet-l') {
      font-size: variables.$font-size--desk-h3;
      line-height: variables.$line-height--desk-h3;
    }
  }

  &__content {
    list-style: none;
    @include mixins.boxGrid(auto, auto, 16px, 16px);

    @include mixins.mediaCustom('min-width: 576px') {
      @include mixins.boxGrid(auto auto, auto, 32px, 16px);
    }

    @include mixins.media('tablet') {
      @include mixins.boxGrid(auto auto auto, auto, 40px, 24px);
    }

    @include mixins.media('desktop') {
      @include mixins.boxGrid(auto auto auto auto, auto, 60px, 32px);
    }
  }

  &__block {
    @include mixins.boxFlex(initial, initial, column);
  }

  &__letter, &__link {
    display: inline-block;
    width: 100%;
    text-align: center;
  }

  &__letter {
    margin: 0 0 11px;
    font-weight: variables.$font-semibold;
  }

  &__link-container {
    display: inline-flex;
    justify-content: center;
    margin: 4px auto;
  }

  &__link-container + br {
    display: none;
  }

  &__link {
    display: inline;
    color: variables.$color-black;
    text-decoration: none;
    font-size: variables.$font-size--general-up-l;
    font-weight: variables.$font-light;
    @include mixins.linkEffect(currentColor, 1px, false);
  }
}

.breadcrumbs-section + .collage-custom {
  margin-top: 40px;

  @include mixins.media('tablet-l') {
    margin-top: variables.$margin-section;
  }

  .collage-custom__title {
    margin-bottom: 40px;
    padding: 0 16px;
    color: variables.$color-black;

    @include mixins.media('tablet-l') {
      margin-bottom: variables.$margin-section;
    }
  }
}
