@use "../abstracts/variables";
@use "../abstracts/mixins";

.cards-content {
  @media (max-width: 750px) {
    overflow-x: hidden;
  }

  &__title {
    text-align: center;
    font-weight: 400;
  }

  &__text {
    margin: 0 auto variables.$margin-l;
    text-align: justify;
  }

  &__container {
    column-gap: 24px;

    @include mixins.mediaCustom('max-width: 750px') {
      @include mixins.boxFlex(flex-start, stretch);
      @include mixins.marginsInside();
      overflow-x: scroll;
      overflow-y: hidden;
      margin-left: 0;
      margin-right: 0;
      grid-column-gap: 0;
      column-gap: 0;
    }

    .cards-content__link {
      min-width: 153px;
      padding: 10px 3px;
    }

    &--grid {
      @include mixins.media('tablet') {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 24px;

        @media (max-width: 1250px) {
          grid-row-gap: 24px;
          row-gap: 24px;
          grid-template-columns: 1fr 1fr;
          grid-template-rows: 1fr 1fr;
        }
      }
    }

    &--flex {
      @include mixins.media('tablet') {
        @include mixins.boxFlex(center, stretch);
      }
      .cards-content__figure {
        flex: 1 1 0;
        max-width: 252px;
        min-width: 252px;

        @include mixins.media('tablet') {
          width: 0;
          min-width: unset;
        }
      }
    }
  }

  &__figure {
    width: 252px;
    flex-shrink: 0;
    padding: 121px 30px 22px;
    position: relative;
    margin-right: 24px;
    @include mixins.boxFlex(space-between, center, column);

    @include mixins.media('tablet') {
      margin-right: 0;
      width: auto;
      flex-shrink: 1;
    }


    &:last-child {
      margin: 0;
    }

    & > * {
      position: relative;
    }

    .cards-content__subtitle {
      width: 100%;
      padding: 22px 16px;
      background-color: variables.$color-white;
      text-align: center;
      margin-bottom: 46px;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
