@use "../abstracts/variables";
@use "../abstracts/mixins";

.tabs-grid {
  &__title {
    text-align: center;
    font-weight: 400;
  }

  &__content {
    &.swiper-slide {
      transition: opacity .3s ease-in-out;
    }

    &:not(.swiper-slide-active) {
      opacity: 0;
    }

    @include mixins.boxGrid(
      auto,
      auto,
      34px,
      34px
    );

    @include mixins.media('tablet') {
      @include mixins.boxGrid(
        auto auto,
        auto,
        34px,
        34px
      );
    }
  }

  &__navigation {
    @include mixins.boxFlex();
    margin-bottom: variables.$margin-l;

    &-btn {
      width: 50%;
      padding: 16px;

      @include mixins.btn-type(
      var(--color-btn-color-2),
      var(--color-btn-bg-2),
      var(--color-btn-border-2)
    );

      @include mixins.media('tablet') {
        width: auto;
        padding: 16px 54px;
      }

      &.active {
        @include mixins.btn-type(
          var(--color-btn-color-1),
          var(--color-btn-bg-1),
          var(--color-btn-border-1)
        );
      }
    }

    button:nth-child(2) {
      margin-left: 26px;
    }
  }

  &__card {
    @include mixins.boxFlex(flex-start);
    align-items: flex-start;

    &-content {
      padding-left: variables.$paddings-side--movil;

      .link-vanilla {
        margin-top: 18px;

        & svg {
          width: 0;
          height: 0;
        }

        & span {
          margin: 0;

          &::after {
            bottom: 0;
          }
        }
      }
    }

    &-subtitle {
      font-size: variables.$font-size--general-l;
      line-height: variables.$line-height--general-m;
      font-weight: variables.$font-regular;
      margin-bottom: 10px;
    }
  }
}
