@use "../abstracts/variables";
@use "../abstracts/mixins";

.box-customize {  
  &__container {
    padding: variables.$margin-l;
    background-color: variables.$color-secondary;
  }

  &__text {
    margin-bottom: 22px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-weight: variables.$font-regular;
    
    @include mixins.media('tablet') {
      max-width: 704px;
    }
  }

  &__link {
    display: block !important;
    width: fit-content;
    margin: 0 auto;
  }
}
