@use '../abstracts/variables';
@use '../abstracts/mixins';

.main-contact {
  position: relative;

  &__title {
    font-weight: 400;
    margin-bottom: 24px;
    text-align: center;
  }

  &__description {
    text-align: center;
  }

  &__actions {
    display: grid;
    grid-template-columns: 1fr;
    grid-column-gap: 24px;
    column-gap: 24px;
    grid-row-gap: 24px;
    row-gap: 24px;

    margin-top: 24px;
    max-width: 562px;
    margin: 0 auto;
    margin-top: 24px;
    padding-left: variables.$paddings-side--movil;
    padding-right: variables.$paddings-side--movil;

    @include mixins.mediaCustom('min-width: 576px') {
      grid-template-columns: 1fr 1fr;
    }
  }

  &__button {
    padding: 16px 23px;
    border: 1px solid variables.$color-principal;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    cursor: pointer;

    & > img {
      margin-right: 16px;
    }

    .text-wrapper {
      color: variables.$color-principal;

      & > * {
        display: block;
        font-weight: 400;
      }

      &__title {
        font-size: 17px;
        line-height: 28px;
        font-family: variables.$font-family--title;
      }

      &__description {
        margin-top: 5px;
        word-break: break-all;

        ol {
          padding-left: 12px;
        }
        ul {
          padding-left: 15px;
        }
      }
    }
  }

  & > .letter-icon {
    top: 80px;
    display: none;

    @include mixins.media('desktop') {
      display: block;
    }
  }

  &__modal {
    background-color: variables.$color-white;
    padding: 48px 24px;
    box-sizing: border-box;
    flex-direction: column;

    .icon-close {
      margin: 0 auto 25px;
      cursor: pointer;
    }

    @media (min-width: 1000px) {
      width: 436px;
      height: 657px;
    }

    &[data-active="true"] {
      display: flex;
    }

    & .main-contact__title {
      display: block;
      margin-bottom: 24px;
    }
  }

  #request-type {
    padding: 7px 12px;
  }
  #request-type,
  #message {
    font-size: variables.$font-size--general-up-s;
  }
  .request-type--clicked,
  #message {
    text-transform: none;
    color: variables.$color-black;

    &::placeholder {
      text-transform: uppercase;
    }
  }
}
