@use "../abstracts/variables";
@use "../abstracts/mixins";

.legal {
  margin: variables.$margin-xxl 0 32px;

  @include mixins.media('tablet-l') {
    margin: variables.$margin-section auto 48px;
  }
  @include mixins.media('desktop') {
    width: 1180px;
  }

  .account-sidebar__mobile .custom-select__items {
    height: auto;
    max-height: 900px;
    transition: max-height .3s ease-in-out;
  }

  .account-sidebar__mobile .account-sidebar__options[data-active=false] .custom-select__items {
    height: auto;
    max-height: 0;
  }

  .account-sidebar__mobile {
    display: block;
  }

  .account-sidebar li {
    padding: 0;
    transition: background-color .3s ease-in-out, color .3s ease-in-out;
  }

  .account-sidebar li a {
    display: inline-block;
    padding: 8px 16px;
  }

  .account-sidebar li:hover {
    color: variables.$color-principal;
  }
}

.legal-content {
  font-size: variables.$font-size--general-m;
  line-height: variables.$line-height--general-m;

  @include mixins.media('tablet-l') {
    font-size: variables.$font-size--general-l;
  }

  p {
    margin-bottom: 6px;
  }

  p:empty {
    margin-bottom: variables.$margin-l;
  }

  p:last-child {
    margin-bottom: 0;
  }

  a, a:visited {
    color: variables.$color-principal;
  }
}

.legal-menu {
  position: static;
  margin: 0 0 32px!important;
  padding: 0;

  @include mixins.media('tablet-l') {
    min-width: 343px;
    margin: 0 auto 48px!important;
  }
}

.breadcrumbs-section {
  margin-bottom: 10px;

  @include mixins.media('tablet-l') {
    max-width: 1180px;
    margin-bottom: 30px;
  }
}
