@use "../abstracts/mixins";

.content-stamp {
  position: relative;

  &__title {
    font-weight: 400;
    font-size: 28px;
    line-height: 40px;
    margin-bottom: 24px;
    text-align: center;
  }

  &__description {
    text-align: justify;
  }

  & > .letter-icon {
    top: -40px;
    display: none;

    @include mixins.media('desktop') {
      display: block;
    }
  }
}
